.hero {

  &__content {
  display: flex;
  min-height: 650px;
  outline: 1px solid grey;
  background-color: lightgrey;
  }

  &__title-block {
    margin-left: auto;
    max-width: 320px;
    right: 0;
    margin-right: 120px;
    margin-top: -485px;
    margin-bottom: 150px;
  }

  &__title {
    margin: 0;
    margin-bottom: 60px;
    font-size: 52px;
    font-weight: 400;
  }

  &__termin-link {
    display: inline-block;
    margin-bottom: 100px;
    &:hover {
      opacity: 0.5;
    }
  }

  @include mobile {    //////////////////////////////  mobile

    &__content {
     margin-bottom: 20px;
     min-height: auto;
      }

    &__title-block {
      max-width: 300px;
      margin: 0 auto;
    }

    &__title {
      text-align: center;
      margin-bottom: 20px;
    }

    &__termin-link {
      display: block;
      text-align: center;
    }
  }
}
