.have {
  background-color: var(--light-color);

  &__text {
    margin: 0;
    padding: 0;
    padding-top: 130px;
    font-weight: 400;
    font-size: 25px;
    padding-bottom: 20px;
  }

  &__title {
    margin: 0;
    padding: 0;
    font-size: 50px;
    font-weight: 300;
    padding-bottom: 80px;
  }

  &__list {
    display: grid;
    grid-template-columns: repeat(4, 1fr);
    gap: 15px;
  }

  &__item {
    display: flex;
    flex-direction: column;
  }

  @include tablet {  /////////////////////  tablet
    &__title {
      font-size: 40px;
      padding-bottom: 50px;
    }

    &__list {
      gap: 10px;
      grid-template-columns: repeat(3, 1fr);
    }
  }

  @include mobile {  /////////////////////  mobile
    &__list {
      grid-template-columns: repeat(3, 1fr);
      gap: 5px;
    }
  }
}
