.best-dienst {
  background-color: var(--background-color);

  &__title {
    margin: 0;
    padding: 0;
     max-width: 600px;
     font-size: 60px;
     font-weight: 400;
     margin-bottom: 80px;
  }

  &__blocks {
    display: flex;
    min-height: 550px;
    margin-bottom: 180px;
    outline: 1px solid var(--dark-color);
  }

  &__block {
    &--right {
    width: 50%;
    display: flex;
    flex-direction: column;
    justify-content: center;
    align-items: center;
    }

    &--left {
      width: 50%;
      background-color: lightgrey;
      background-image: url("../img/woman3.jpg");
      background-repeat: no-repeat;
      background-size: cover;
    }
  }

  &__block-title {
    margin: 0;
    padding: 0;
    font-size: 25px;
    font-weight: 400;
    margin-bottom: 20px;
  }

  &__block-text {
    margin: 0;
    padding: 0;
    margin-bottom: 20px;
  }

  &__link {
    font-size: 16px;
    font-weight: 300;
    background-color: var(--text-color);
    color: var(--light-color);
    padding: 12px 60px;
    &:hover {
      opacity: 0.5;
    }
  }

  @include tablet {    ///////////////////// mobile
    &__title {
      font-size: 35px;
      margin-bottom: 40px;
    }

    &__blocks {
      display: flex;
      flex-direction: column;
      margin-bottom: 80px;
    }

    &__block {
      &--right {
      display: flex;
      width: 100%;
      }
      &--left {
        min-height: 300px;
        margin-bottom: 40px;
        width: 100%;
      }
    }
  }
}
