.info {
  &__block {
    max-width: 700px;
    margin: 0 auto;
    padding: 0;
    text-align: center;
  }

  &__title {
    margin: 0;
    padding: 0;
    font-family: Playfair;
    font-size: 60px;
    font-weight: 400;
    margin-top: 80px;
    margin-bottom: 40px;
  }

  &__text {
    margin: 0;
    padding: 0;
    font-size: 18px;
    line-height: 30px;
    margin-bottom: 80px;
  }

  &__picture {
    min-height: 800px;
    outline: 1px solid grey;
    margin-bottom: 80px;
    background-image: url("../img/woman3.jpg");
    background-repeat: repeat;
    background-size: cover;
  }
}
