.reviews {
  background-color: var(--color-block);

  &__list {
    min-height: 750px;
    display: flex;
    justify-content: space-between;
    padding-top: 80px;
    padding-bottom: 80px;
  }

  &__item {
    display: flex;
    justify-content: center;
    align-items: center;
    width: 32%;

    &--left {
      background-color: var(--light-color);
    }

    &--center {
      background-color: var(--dark-color);
    }

    &--right {
      background-color: var(--light-color);
    }
  }

  &__title {
    margin: 0;
    font-size: 60px;
    font-weight: 300;
    text-align: center;
  }

  &__item-text {
    width: 48%;
    font-size: 18px;
    line-height: 28px;
    padding-top: 15px;
    padding-bottom: 15px;
  }

  @include tablet {  //////////////////    tablet
    background-color: transparent;

    &__list {
      min-height: 450px;
      padding-top: 0;
      padding-bottom: 0;
    }

    &__item {
      display: flex;
      width: 100%;
      &--left {
        background-color: var(--color-block);
      }
      &--center {
        min-height: 150px;
      }
      &--right {
        background-color: var(--light-color);
      }
      justify-content: flex-start;
    }

    &__item-text {
      width: 80%;
      text-align: center;
      margin: 0 auto;
      font-weight: 100;
    }

    &__title {
      margin: 0 auto;
      font-size: 40px;
      font-weight: 300;
      padding-top: 40px;
      padding-bottom: 40px;
    }
  }

  @include tablet {   ///////////////////////      mobile
    &__container {
      padding: 0;
    }

    &__list {
      display: flex;
      flex-direction: column;
      min-height: auto;
    }

    &__title {
      max-width: 350px;
      text-align: start;
      margin: 0;
      padding-left: 30px;
    }

    &__item-text {
      text-align: start;
      margin: 0;
      padding-left: 30px;
      padding-top: 40px;
      padding-bottom: 40px;
    }

    &__item-name {
      display: inline-block;
      padding-top: 20px;
    }
  }
}
