.footer {
  background-color: var(--dark-block);
  min-height: 400px;
  color: var(--light-color);

  &__blocks {
    display: flex;
    justify-content: space-between;
    padding-top: 80px;
  }

  &__block {
    &--right {
      display: flex;
    }
  }

  &__social {
    &:not(:last-of-type){
      margin-bottom: 35px;
    }
  }

  &__logo {
    display: inline-block;
    color: var(--light-color);
    font-size: 30px;
    margin-bottom: 100px;
  }

  &__copyright {
    display: flex;
    margin-bottom: 100px;
  }

  &__copy {
    margin: 0;
    padding-bottom: 30px;
  }

  @include tablet {   ///////////////////   tablet
    &__logo {
      margin-bottom: 50px;
    }

    &__blocks {
      display: flex;
      flex-direction: column;
      padding-top: 30px;
    }

    &__nav {
      display: none;
    }

    &__social {
      display: flex;
      &:not(:last-of-type){
        padding-right: 20px;
      }
    }

    &__copyright {
      margin-bottom: 20px;
    }

    &__copy {
      margin: 0;
      padding-bottom: 30px;
    }
  }
}
