.pictures {

  margin-bottom: 120px;
  background-color: var(--dark-block);

  &__blocks {
    display: flex;
    justify-content: space-between;
    padding-top: 120px;
    padding-bottom: 100px;
  }

  &__item {
    width: 31%;
    display: flex;
    flex-direction: column;
    align-items: center;
    color: var(--light-color);
  }

  &__image {
    margin-bottom: 60px;
    background-color: lightgrey;
    background-repeat: no-repeat;
    background-size: cover;
  }

  &__title {
    margin: 0;
    margin-bottom: 30px;
    padding: 0;
    font-weight: 400;
  }

  &__text {
    margin: 0;
    padding: 0;
    font-weight: 300;
    max-width: 70%;
    text-align: center;
  }

  &__link-block {
    display: flex;
    justify-content: center;
    padding-bottom: 60px;
  }

  &__dienstleistungen-link {
    background-color: var(--light-color);
    color: var(--dark-block);
    transition: color 0.5s ease;
    transition: background-color 0.5s ease;
    &:hover {
      color: white;
      background-color: var(--dark-block);

    }
  }

  @include mobile {   /////////////////////// mobile

    &__container {
      margin: 0;
      padding: 0;
    }
    margin-bottom: 80px;

    &__blocks {
    flex-direction: column;
    padding-top: 0;
    }

    &__item {
      width: 100%;
      &:not(:last-child){
        margin-bottom: 60px;
      }
    }
  }
}
