.kontakt {

  &__list {
    display: grid;
    grid-template-columns: repeat(3, 1fr);
    gap: 20px;
  }

  &__list {
    min-height: 600px;
    margin-bottom: 80px;
    font-size: 18px;
    line-height: 25px;
  }

  &__block {
    max-width: 700px;
    margin: 0 auto;
    padding: 0;
    text-align: center;
  }

  &__title {
    margin: 0;
    padding: 0;
    font-family: Playfair;
    font-size: 60px;
    font-weight: 400;
    margin-top: 80px;
    margin-bottom: 40px;
  }

  &__text {
    margin: 0;
    padding: 0;
    font-size: 18px;
    line-height: 30px;
    margin-bottom: 80px;
  }

  &__item {
    display: flex;
    flex-direction: column;
    align-items: center;
    justify-content: center;
  }

  &__item {
    &--left {
      color: var(--light-color);
      background-color: var(--dark-color);
    }
    &--center {
      background-color: var(--color-block);
    }
    &--right {
      color: var(--light-color);
      background-color: var(--dark-color);
    }
  }

  &__descr {
    margin: 0;
    padding: 0;
    font-weight: 500;
    font-size: 24px;
    margin: 20px 0;
  }

  &__phone {
    margin-bottom: 40px;
    color: var(--light-color);
  }

  &__email {
    margin-bottom: 40px;
    color: var(--light-color);
  }

  &__open-time {
    display: flex;
    flex-direction: column;
  }

  &__open-text {
    margin: 0;
    padding: 0;
    padding-bottom: 10px;
  }

  &__adresse-text {
    margin: 0;
    padding: 0;
  }

  @include tablet {   /////////////////////// tablet
    &__list {
      display: flex;
      flex-direction: column;
    }

    &__adresse-text--stadt {
      margin-bottom: 20px;
    }
  }
}
