.dienst {

  &__title {
    margin: 0;
    padding: 0;
    font-family: Playfair;
    font-size: 70px;
    font-weight: 400;
    margin-top: 80px;
    margin-bottom: 40px;
    text-align: center;
  }

  &__descr {
    display: flex;
    justify-content: center;
  }

  &__descr li {
    padding-right: 40px;
    &:first-child {
      padding-left: 40px;
    }
    border-bottom: 1px solid black;
    margin-bottom: 80px;
  }

  &__name {
    font-weight: 400;
    font-size: 25px;
  }

  &__list {
    display: flex;
    justify-content: center;
    flex-direction: column;
    width: 50%;
    margin: auto;
    margin-bottom: 80px;
  }

  &__item {
    display: flex;
    justify-content: space-between;
    border-top: 1px solid var(--dark-color);
    border-bottom: 1px solid var(--dark-color);
    margin-bottom: -1px;
    padding: 40px;
  }

  &__review {
    margin: 0;
    padding: 0;
    font-size: 25px;
    font-weight: 300;
  }

  &__text {
    margin: 0;
    font-size: 18px;
    font-weight: 300;
  }

  &__picture {
    min-height: 800px;
    outline: 1px solid grey;
    margin-bottom: 80px;
    background-image: url("../img/woman3.jpg");
    background-repeat: repeat;
    background-size: cover;
  }
  @include tablet {  ///////////////////////////////////////////////   tablet

  &__list {
    width: 100%;
  }

  &__item {
    flex-direction: column;
    align-items: center;
    padding-left: 0;
    padding-right: 0;
  }
}
}
