.adresse {

  &__text {
    margin: 0;
    padding: 0;
    line-height: 140%;
  }

  &__item--street {
    margin-bottom: 30px;
  }

  &__item--phone {
    margin-bottom: 30px;
    font-size: 20px;
  }

  &__number {
    color: var(--light-color);
    &:hover {
      opacity: 0.7;
    }
  }

  &__email {
    color: var(--light-color);
    &:hover {
      opacity: 0.7;
    }
  }

  @include tablet {
    &__item {
      &--email {
      margin-bottom: 30px;
      }
    }
  }
}
