.header {
    padding: 30px 0;

    &__container {
        @include flex-all-sb;
    }

    &__nav {
      color: var(--text-color);
      font-weight: 300;
    }

    &__logo {
      color: var(--text-color);
    }

    &__number {
      color: var(--dark-color);
      font-size: 20px;
      font-weight: 400;
      &:hover {
        opacity: 0.5;
      }
    }
}

