.social {
  &__instagram {
    display: block;
    width: 20px;
    height: 20px;
    background-size: 20px 20px;
    background-image: url("../img/instagram.svg");
    background-repeat: no-repeat;
    background-size: cover;
  }

  &__item {
    margin: 0;
    padding: 0;
  }

  @include tablet {
    &__instagram {
      width: 40px;
      height: 40px;
      background-size: 40px 40px;
    }
  }

  &__item {
    margin-bottom: 0;
  }
}
